import { Link } from "react-router-dom"
import PageTitle from "./Components/PageTitle"
import Table from "./Components/Table"
import { useCallback, useEffect, useState } from "react"
import { get, patch } from "../Functions/Api"
import ButtonCheck from "./Components/Buttons/ButtonCheck"
import { ConfirmAlert, PopupAlert } from "../Functions/Alerts"
import ButtonReject from "./Components/Buttons/ButtonReject"

const DataPendaftar = () => {
    const [dataPendaftar, setDataPendaftar] = useState<DataT[]>()
    const [pendaftar, setPendaftar] = useState<boolean>(true)

    const getDataPendaftar = useCallback(async () => {
        const path = pendaftar ? "data-pendaftar" : "data-pendaftar-kadaluarsa"
        const res = await get(path)
        if (res.ok) setDataPendaftar(res.data)
    }, [pendaftar])

    useEffect(() => {
        getDataPendaftar()
    }, [getDataPendaftar])

    const RowHead = (
        <tr>
            <th>No</th>
            <th>Nomor KK</th>
            <th>Nama Kepala Keluarga</th>
            <th>NIK Kepala Keluarga</th>
            <th colSpan={2}>ACTION</th>
        </tr>
    )

    const activation = (evt: React.MouseEvent<HTMLButtonElement>) => {
        const nik = evt.currentTarget.dataset?.nik

        ConfirmAlert(
            {
                icon: "question",
                title: "Setujui?",
                text: "Apakah anda menyetujui pembuatan akun ini ? Akun yang sudah disetujui tidak bisa dirubah kembali !",
                confirmButtonText: "Setujui"
            },
            async () => {
                const res = await patch(`pendaftar/${nik}/aktivasi`)
                if (res.ok) {
                    PopupAlert.fire({
                        icon: "success",
                        text: res.data?.message
                    })
                    getDataPendaftar()
                }
            }
        )
    }

    const reject = (evt: React.MouseEvent<HTMLButtonElement>) => {
        const nik = evt.currentTarget.dataset?.nik
        const rejectButton = () => {
            ConfirmAlert(
                {
                    input: "textarea",
                    inputLabel: "Pesan",
                    inputPlaceholder: "Ketik pesanmu disini...",
                    inputAttributes: {
                        "aria-label": "Type your message here"
                    },
                    showCancelButton: false,
                    confirmButtonText: "Kirim"
                },
                async () => {
                    const res = await patch(`pendaftar/${nik}/tolak`)
                    if (res.ok)
                        PopupAlert.fire({
                            icon: "success",
                            text: res.data?.message
                        })
                }
            )
        }

        ConfirmAlert(
            {
                icon: "question",
                title: "Tolak?",
                text: "Apakah anda menolak pembuatan akun ini ? Jika iya, silahkan tekan tombol Tolak",
                confirmButtonText: "Tolak",
                confirmButtonColor: "danger"
            },
            rejectButton
        )
    }

    return (
        <>
            <PageTitle title="Data Pendaftar" />
            <div className="d-flex gap-3">
                <Link
                    className={pendaftar ? `text-decoration-underline` : ""}
                    to="#"
                    onClick={() => setPendaftar((current) => !current)}
                >
                    Data Pendaftar
                </Link>
                <Link
                    className={!pendaftar ? `text-decoration-underline` : ""}
                    to="#"
                    onClick={() => setPendaftar((current) => !current)}
                >
                    Data Pendaftar Lewat 2x 24 Jam
                </Link>
            </div>
            <Table
                title="Tabel Data Pendaftar"
                rowThead={RowHead}
                search={{
                    url: "data-pendaftar?search=:keyword",
                    dispatch: setDataPendaftar
                }}
            >
                {dataPendaftar &&
                    dataPendaftar.map((pendaftar, i) => {
                        const { no_kk, nama, nik } = pendaftar.user

                        return (
                            <tr key={i}>
                                <td className="text-bold-500">{++i}</td>
                                <td>{no_kk}</td>
                                <td>{nama}</td>
                                <td>{nik}</td>
                                <td className="d-flex justify-content-center align-items-center gap-1">
                                    <ButtonCheck
                                        props={{ onClick: activation }}
                                        nik={nik}
                                    />
                                    <ButtonReject
                                        props={{ onClick: reject }}
                                        nik={nik}
                                    />
                                </td>
                            </tr>
                        )
                    })}
            </Table>
        </>
    )
}

export default DataPendaftar
