const ButtonReject = ({
    props,
    nik,
    id
}: {
    props?: React.ButtonHTMLAttributes<HTMLButtonElement>
    nik?: string
    id?: string
}) => {
    return (
        <button
            type="button"
            className="text-danger btn p-0"
            data-nik={nik}
            data-id={id}
            {...props}
        >
            <i className="bi bi-x fs-2 lh-1" data-feather="delete-data" />
        </button>
    )
}

export default ButtonReject
