const ButtonCheck = ({
    props,
    nik
}: {
    props: React.ButtonHTMLAttributes<HTMLButtonElement>
    nik: string
}) => {
    return (
        <button
            type="button"
            className="text-success btn p-0 border-0"
            data-nik={nik}
            {...props}
        >
            <i className="bi bi-check fs-2 lh-1" />
        </button>
    )
}

export default ButtonCheck
